.home {
    height: 100%;
    padding: 40px;

    background: #303030;

    display: grid;
    grid-template-rows: 0.1fr 0.6fr 0.2fr 0.1fr;
}

.home_top {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.top_avi {
    width: 52px;
    height: 52px;

    background: #39dab1;
    border-radius: 50%;

    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.top_avi img {
    width: 48px;
    height: 48px;

    border-radius: 50%;
}

.top_icons {
    color: #ffffff;
    font-weight: 700;
    font-size: 1em;

    display: flex;
}

.home_projects {
    overflow-x: auto;

    display: flex;
    flex-wrap: nowrap;
    align-items: center;
}

.project {
    margin: 0 10px;

    flex: 0 0 auto;
    display: flex;
    flex-flow: column;
    justify-content: center;
}

.project a {
    width: 250px;
    height: 250px;

    background: #464646;

    display: flex;
    justify-content: center;
    align-items: center;
}

.project a:hover {
    border: 2px solid #51eff5;
}

.project > a img {
    width: 248px;
}

.home_links {
    display: flex;
    justify-content: center;
}

.home_link {
    margin: 0 10px;

    display: flex;
    flex-flow: column;
}

.home_link .link_img {
    width: 100px;
    height: 100px;

    display: flex;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.home_link .link_img a {
    width: 85%;

    background: #464646;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;

    transition: 0.5s;
}

.home_link .link_img a:hover {
    width: 100%;
}

.home_link  .link_img a img {
    padding: 5px;

    width: 100%;
}

::-webkit-scrollbar {
    display: none;
}