.landing_page {
    height: 100%;

    background: #303030;

    display: grid;
    grid-template-columns: 0.3fr 0.7fr;
}

.quick_links {
    height: 100%;
    padding: 25px;

    font-size: 2rem;
    text-align: center;
    color: #ffffff;
    background: #181818;

    display: grid;
    grid-template-rows: 0.1fr auto;
}

.quick_links p {
    padding: 15px 0;
}

.quick_links_nav {
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    align-items: center;
}

.quick_links_nav a {
    width: 100%;
    height: 175px;

    color: #000000;
    text-decoration: none;
    background: #ffffff;
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.quick_links_nav a:nth-child(1) {
    margin-top: 15px;
}

.quick_links_nav a:nth-child(3) {
    margin-bottom: 15px;
}

.intro {
    display: grid;
    grid-template-rows: 0.05fr auto;
}

.intro .text {
    display: flex;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.intro .text a {
    position: relative;
    z-index: 1;

    padding: 0 10px;

    background: linear-gradient(to right, #9ddff3, #9ddff3) no-repeat right;
    background-size: 0% auto;

    font-size: 3em;
    font-weight: 700;
    text-decoration: none;
    color: #ffffff;

    transition: background-size 0.3s, background-position 0s;
}

.intro .text a:hover {
    background-size: 100% auto;
    background-position: left;
}

.intro .text a span {
    mix-blend-mode: difference;
}

.intro > span {
    padding-top: 5px;
    padding-right: 5px;
    
    color: #ffffff;
    font-size: 1.5em;
    text-align: end;
}

